import React, { PureComponent, PropTypes } from 'react';
import classNames from 'classnames/bind';
import TransitionGroup from 'react-transition-group/TransitionGroup'
import styles from './SignatureContainer.scss';
const cx = classNames.bind(styles);

const style = {
  marginRight: 20,
};

import SignatureHintMask from './components/SignatureHintMask/SignatureHintMask.jsx';
import Signature from '../../components/Signature/Signature.jsx';
import animeSentOut from './animation/aniSentOutContract.js';
// FIXME: safari
// TODO: landscape mount animation stop

/**
 * feature:
 * 1. opening hint step: contract scale by transform
 * 2. write signature: 
 *      => viewport zoom out 
 *      => signature zoom in 
 *      => orientation landscape hint ( cover by mask can't be draw, by css )
 *      => screen overflow lock
 * 
 * @class SignatureContainer
 * 
 * @extends {PureComponent}
 */
class SignatureContainer extends PureComponent {

  constructor() {
    super();
    this.state = {
      orientation: false,
      hintMaskDone: false,
      signatureAreaOpen: false,
      signatureComplete: false,
      area_proportion: false,
      imageSignature: false,
      zoomIn: false,
      contractOnload: false,
    }
  }

  _getOrientation = () => {
    this.setState({
      orientation: window.innerHeight > window.innerWidth ? 'port' : 'land'
    });
  }

  componentDidMount() {
    this._getSignatureAreaProportion();
    var viewport_meta = document.querySelector('meta[name="viewport"]');
    document.body.addEventListener('gesturestart', function() {
      viewport_meta.content = 'width=1040';
    }, false);

    window.addEventListener("resize", (e) => {
      this._getOrientation();
    });
  }

  _getSignatureAreaProportion = () => {
    this.setState({
      area_proportion: this.refSignatureArea.clientWidth / this.refSignatureArea.clientHeight
    });
  }

  _zoomOutViewSize = () => {
    var viewport_meta = document.querySelector('meta[name="viewport"]');
    if (viewport_meta) {
      viewport_meta.content = 'width=1041, user-scalable=no'; // width 1040 will make user-scalable useless
    }
  }

  _releaseScalable = () => {
    var viewport_meta = document.querySelector('meta[name="viewport"]');
    viewport_meta.content = 'width=1040';
  }

  _cbHintDone = () => {
    this.setState({
      hintMaskDone: true
    }, () => {
      this._releaseScalable()
    })
  }

  _openSignatureArea = () => {
    this._zoomOutViewSize();
    this.setState({
      signatureAreaOpen: true
    });
  }

  _closeSignatureArea = (imageSignature, signedOrNot) => {
    this.setState({
      signatureAreaOpen: false,
      signatureComplete: signedOrNot,
      imageSignature
    }, () => {
      this._releaseScalable();
    });
  }

  _completeSignature = (bool) => {
    this.setState({
      signatureComplete: bool
    });
  }

  _confirmContract = () => {
    animeSentOut(this.refContract)
  }

  _onloadImgContract = () => {
    this.setState({
      contractOnload: true
    });
  }

  render() {
    const id_proposer = "signature_proposer";
    const id_contract = "box_contract";
    const {img_contract} = this.props;
    const {orientation, hintMaskDone, signatureAreaOpen, area_proportion, imageSignature, signatureComplete, contractOnload} = this.state;
    return (
      <div className={ cx("container", {
                   "active": imageSignature
                 }) }>
        <div
          id={ id_contract }
          ref={ contract => this.refContract = contract }
          className={ cx('box__contract', {
                        'box__contract--hint': !hintMaskDone
                      }) }>
          <img
            src={ img_contract }
            className={ styles.img__contract }
            onLoad={ this._onloadImgContract }
            alt="contract" />
          <div
            id={ id_proposer }
            ref={ signature_area => this.refSignatureArea = signature_area }
            className={ cx('signature_position', 'signature_requirement', {
                          'active': imageSignature
                        }) }
            style={ { backgroundImage: `url(${imageSignature})` } }
            onClick={ this._openSignatureArea }></div>
        </div>
        { area_proportion ?
          <Signature
            dom_signature_id={ id_proposer }
            orientation={ orientation }
            signature_open={ signatureAreaOpen }
            area_proportion={ area_proportion }
            closeSignature={ this._closeSignatureArea } /> : false }
        <TransitionGroup>
          { !hintMaskDone &&
            <SignatureHintMask
              orientation={ orientation }
              cbHintDone={ this._cbHintDone }
              id_contract={ id_contract }
              contractOnload={ contractOnload } /> }
        </TransitionGroup>
        <div className={ cx("btn__confirm", {
                           "active": signatureComplete
                         }) } onClick={ this._confirmContract }>
          
        </div>
      </div>
      );
  }
}

SignatureContainer.propTypes = {
  img_contract: PropTypes.string.isRequired
};

export default SignatureContainer;



