import React, { PureComponent, PropTypes } from 'react';
import styles from './SignatureHintMask.scss';
import { TweenMax } from 'gsap';

import aniLight from './animation/aniLight.js';


class SignatureHintMask extends PureComponent {

  constructor() {
    super();
    this.state = {
      vw: window.innerWidth,
      vh: window.innerHeight,
      logo_size: window.innerHeight / 3,
      logo_x: window.innerWidth / 2 - window.innerHeight / 6,
      logo_y: window.innerHeight * 0.2,
    }
  }

  componentWillUpdate(nextProps, nextState) {
    if (nextProps.orientation != this.props.orientation) {
      this._viewportInit();
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.orientation === "port" && prevProps.orientation == "land") {
      this._startOpeningAnimation();
    }
  }

  componentDidMount() {
    if (window.innerHeight > window.innerWidth) {
      // delay for user breath
      setTimeout(() => {
        this._startOpeningAnimation();
      }, 1000);
    }
  }

  componentWillLeave(cb) {
    TweenMax.to(this.refContainer, 0.5, {
      opacity: 0,
      onComplete: cb
    })
  }

  _viewportInit = () => {
    this.setState({
      vw: window.innerWidth,
      vh: window.innerHeight,
      logo_size: window.innerHeight / 3,
      logo_x: window.innerWidth / 2 - window.innerHeight / 6,
      logo_y: window.innerHeight * 0.2,
    }, this._startOpeningAnimation);
  }

  /**
   * when orientation is portrait and contract image onload 
   * then active the opening animation
   * @memberof SignatureHintMask
   */
  _startOpeningAnimation = () => {
    if (window.innerHeight > window.innerWidth && this.props.contractOnload) {
      aniLight(this.refCircleLight, this.refTitle, this.props.id_contract)
    // window.removeEventListener("orientationchange", () => {
    // });
    }
  }

  render() {
    const {cbHintDone} = this.props;
    const {vw, vh, logo_size, logo_x, logo_y} = this.state;
    return (
      <div
        ref={ container => this.refContainer = container }
        className={ styles.container }
        onClick={ cbHintDone }>
        <h1 ref={ title => this.refTitle = title }></h1>
        <svg viewBox={ `0 0 ${vw} ${vh}` }>
          <defs>
            <mask id="clip_hint">
              <rect
                width="100%"
                height="100%"
                fill="white" />
              <circle
                className="svg__hintCircle"
                ref={ circle => this.refCircleLight = circle }
                cx={ vw * 0.5 }
                cy={ -vh * 0.5 }
                r="0"
                fill="black" />
            </mask>
          </defs>
          <rect
            className={ styles.svg__bg }
            x="0"
            y="0"
            width={ window.innerWidth }
            height={ window.innerHeight }
            mask="url(#clip_hint)" />
          <image
            x={ logo_x }
            y={ logo_y }
            width={ logo_size }
            height={ logo_size }
            className={ styles.svg__logo }
            xlinkHref={ require('../../../../images/NEWA/logo_newa.png') } />
        </svg>
      </div>
      );
  }
}

SignatureHintMask.propTypes = {
  cbHintDone: PropTypes.func.isRequired,
  id_contract: PropTypes.string.isRequired,
  contractOnload: PropTypes.bool.isRequired,
  orientation: PropTypes.string.isRequired, // should be 'port' || 'land'
};

export default SignatureHintMask;



